export default function Icon({ w, h, color, rotate, t, r, b, l }) {
  const style = () => {
    return {
      position: "absolute",
      top: { t },
      bottom: { b },
      left: { l },
      right: { r }
    };
  };

  return (
    <svg width={w} height={h} viewBox="0 0 100 100" style={style()}>
      <path
        fill="none"
        transform={rotate}
        style={{
          WebkitTransformOrigin: "50%50%",
          MsTransformOrigin: "50%50%",
          transformOrigin: "50%50%"
        }}
        stroke={color}
        strokeWidth="4"
        d="M10 10L90 10 90 90"
      ></path>
    </svg>
  );
}
